import React from 'react'
import Media from 'react-media'
import { observer } from 'mobx-react'
import store from '../Store'

import Switcher from './_common/Switcher'
import SwitcherPor from './_common/SwitcherPor'
import Badges from './_common/Badges'

import img from '../../assets/projects/simon-main.jpg'
import imgSma from '../../assets/projects/simon-main-sma.jpg'
import imgMed from '../../assets/projects/simon-main-med.jpg'

import check from '../../assets/check.svg'

import './project.sass'

@observer
export default class Simon extends React.Component {
  componentDidMount() {
    document.body.scrollTop = 0
  }
  render() {
    return (
      <div>
        <div className='project' >
        {store.language === 'FR' ?
          <main className='wrapper'>
            <h1 className='title'>{store.Simon.Name}<span className='title--dot'>.</span></h1>
            <Badges store={store} project='Simon' />
            <section className='section-first'>
              <p>Simon est une rplique du jeu de socit des annes 80 dvelopp en Javascript et jQuery. </p>
            </section>

            <img
              src={imgMed}
              srcSet={`${imgSma} 550w,
              ${imgMed} 1000w,
              ${img}  1600w`}
              className='project-ss'
              alt='Simon game' />

            <section  className='section-second'>
              <h2><img src={check} alt='check mark'/> jQuery et Object Oriented pour la structure</h2>
              <p>Les mthodes tant assez nombreuses il tait impratif de bien dclarer chaque objet , j'ai donc choisi d'utiliser jQuery avec un style Object Oriented pour donner une structure plus facile  lire et modifier.</p>
            </section>
            <section  className='section-third'>
              <h2 className='third'><img src={check} alt='check mark'/> Mise en cache des mthodes et optimisation</h2>
              <p className='third'>jQuery peut vite devenir gourmant en ressources, il a donc t ncessaire de mettre en cache le maximum de selections et les binds d'vnement pour minimiser leur impact sur le DOM et ne les soliciter que lors de l'initialisation de chaque objet pour un gain de performance important.</p>
            </section>
            <section  className='section-fourth'>
              <h2 className='third'><img src={check} alt='check mark'/> Pug et Sass pour la lisibilit</h2>
              <p className='third'>Pour continuer dans la simplicit et lisibilit adopte dans la partie javascript j'ai opt pour Pug et Sass pour respectivement le html et css. Le rsultat est un code court et trs dclaratif qui se lit facilement. Ajoutez  a les commentaires et on obtient un code qui sera compris trs rapidement et modifi sans casse tte.</p>
            </section>
          </main>
          :                 <main className='wrapper'>
                              <h1 className='title'>{store.Simon.Name}<span className='title--dot'>.</span></h1>
                              <Badges store={store} project='Simon' isMobile={true} />
                              <section className='section-first'>
                                <p>Simon is a replica of the 80's game developped in Javascript and jQuery.</p>
                              </section>

                              <img
                                src={imgMed}
                                srcSet={`${imgSma} 550w,
                                ${imgMed} 1000w,
                                ${img}  1600w`}
                                className='project-ss'
                                alt='Simon game' />

                                <section  className='section-second'>
                                  <h2><img src={check} alt='check mark'/> jQuery and Object Oriented structure</h2>
                                  <p>With the numerous methods in the code it was crucial that the objects were declared and structured properly. I opted for an Obect Oriented approach with jQuery for an easily readable and modifiable layout.</p>
                                </section>
                                <section  className='section-third'>
                                  <h2 className='third'><img src={check} alt='check mark'/> Method caching and optimisation</h2>
                                  <p className='third'>jQuery can quickly become resource hungry so it was necessary to cache the DOM selections and event bindings to minimize their impact on the DOM therefore only declaring them in the initialization phase for an apparent performance gain.</p>
                                </section>
                                <section  className='section-fourth'>
                                  <h2 className='third'><img src={check} alt='check mark'/> Pug et Sass for lisibility</h2>
                                  <p className='third'>To continue with the simplicity and lisibility goals of the app adopted in the javascript part I opted for Pug and Sass for respectively the html and css. The result is a shorter and very declarative code easiliy readable. Adding to that the code comments and you get a code that will be understood quickly and modified without problems.</p>
                                </section>
                        </main>}
      </div>
      <Media query={{orientation: 'portrait'}}>
          {matches => matches ?
            ( <SwitcherPor project='Simon'/> ) : (  <Switcher project='Simon'/> )}
      </Media>
    </div>
    );
  }
}



